import React from 'react';
import Knob from './Knob';
import Icon from './Icon';

class SynthControls extends React.Component {
  state ={ update: false };
  handleControls(e) {
    console.log(e.target.value);
  }

  handleChange = (value) => {
      
      console.log(value);
  };
 
  setOption = (evt, osc, value, id) => {
    console.log(value);
    
    if (osc) {
      console.log(this.props.songData[this.props.synthId].sound.oscillator.type);
      this.props.songData[this.props.synthId].sound.oscillator.type = String(osc);
    } else {
      if (this.props.songData[this.props.synthId].synth === "Sampler") {
        console.log(evt.target.value);
        this.props.songData[this.props.synthId].sound.volume = evt.target.value;
      } else {
        if (evt) {
          if (evt.target.id === "volume") { this.props.songData[this.props.synthId].sound[evt.target.id] = evt.target.value }

        } else {
          this.props.songData[this.props.synthId].sound.envelope[id] = parseFloat(value / 20)
        }

      }
    }
    
    //this.props.songData.map((item, i) => (this.props.setSynth(i, item.sound, item.synth)))

    // nur für eins setzen!!
    this.props.setSynth(this.props.synthId)
this.setState({ update: !this.state.update });
    
  }

 

  render() {
    if (!this.props.toggleSynthControls) {
      return null;
    }
    let sound =  this.props.songData[this.props.synthId]? this.props.songData[this.props.synthId].sound : [];
    
    const OSCILLATORS = {
      normal: ["sine", "triangle", "square", "sawtooth"],
      special: ["pulse", "fatsine", "fatsquare", "pwm"]
    };
    const knobSize     = 0.1 * 350;
    let url = "";
    if (this.props.songData[this.props.synthId] && this.props.songData[this.props.synthId].url) {
    var regex = /^([\w\/]*?)([\w\.]*)\./;
    var matches = this.props.songData[this.props.synthId].url.match(regex);
    url = matches[2];
    }
    return (
      <div>
      <div className="controls"  >

      
        {Object.keys(sound).filter(item => item === "volume").map((item, key) => {
          return (
            <fieldset key={key} >
              <legend>{item}</legend>
              <div className="control">Level
                <input className="ctrl" style={{ transform: 'rotate(-90deg)' }} type="range" value={sound[item]} min="-50" max="0" onChange={this.setOption} id={item} />
              </div>
            </fieldset>
          );
        })}
    

        <fieldset >
          <legend>Oscillator</legend>
          <div>
          <div className="control">Pitch
            <Knob 
              angle={270}
              size={knobSize}
              ticks={6}
              value={100} min="1" max="300" step="1" onChange={() => this.handleChange()} />
        </div>
          <div  className="control">Waveform
            <span>
              {sound.oscillator ?
                OSCILLATORS.normal.map((item, key) => {
                  return (
                    <button key={key} className={this.props.songData[this.props.synthId].sound.oscillator.type === item ? 'buttons  active' : 'buttons '} onClick={() => this.setOption("", `${item}`)}>
                    <Icon name={item} color="yellow" size="18" /> </button>
                  );
                }) : this.props.songData[this.props.synthId]? this.props.songData[this.props.synthId].synth : null
              }
            </span>  <span>
              {sound.oscillator ?
                OSCILLATORS.special.map((item, key) => {
                  return (
                    <button key={key} className={this.props.songData[this.props.synthId].sound.oscillator.type === item ? 'buttons  active' : 'buttons '} onClick={() => this.setOption("", `${item}`)}><Icon name={item} color="white" size="18" /> </button>
                  );
                }) :  url
              }
              
            </span>
            </div></div>
        </fieldset>
        
        <fieldset>
          <legend>Envelope</legend>
          <div>
            {sound.envelope ?
              Object.keys(sound.envelope).map((item, key) => {
                
                return (
                  <div key={key} className="control">{item}
                    <Knob 
                    value={parseInt(sound.envelope[item] * 10,10)} 
                    min={1} 
                    max={30}
                    step="1" 
                    onChange={(value) => this.setOption("", "", value, item)} 
                    numTicks={15}
                       angle={270}
                       size={knobSize}
                       />
                  </div>

                );
              }) : ""}
          </div>
        </fieldset>

        <fieldset>
          <legend>Filter</legend>
          <div>
            <div className="control">Cutoff
            <Knob value={100} min="1" max="300" step="1" onChange={(value) => console.log(value)} 
               angle={270}
               size={knobSize}
               ticks={6}
               />
            </div>
            <div className="control">Resonance
            <Knob value={100} min="1" max="300" step="1" onChange={(value) => console.log(value)}    angle={270}
              size={knobSize}
              ticks={6}
              />
            </div>
          </div>
        </fieldset>
        </div>
        
     
      </div>
    ) //<input className="ctrl" type="range" defaultValue={sound.envelope[item]} min="0.01" max="3" step="0.01" id={item}/>
  }
}

export default SynthControls;


