import React from 'react';

class Note extends React.Component {
  render() {
    let Style = {
      backgroundColor: 'hsla(' + this.props.id * 25 + ', 40%, 60%,0.7)',
      boxShadow: ' 0px 2px 1px rgba(0,0,0,1),' +
        '0px 1px 3px rgba(0,0,0,.6),' +
        '0px 0px 40px rgba(0,0,0,.5) inset,' +
        '0px 3px 5px rgba(0,0,0,1)'
    }
    let activeStyle = {
      backgroundColor: 'hsla(' + this.props.id * 25 + ', 80%, 60%,1)',
      boxShadow: '0 1px 0px 1px hsla(' + this.props.id * 25 + ', 100%, 20%,1),' +
        ' 0px 0px 5px 3px hsla(' + this.props.id * 25 + ', 80%, 30%,0.3),' +
        ' 0px -5px 20px hsla(' + this.props.id * 25 + ', 80%, 30%,0.1) inset,' +
        ' 0px 0px 22px hsla(' + this.props.id * 25 + ', 80%, 30%,1) inset,' +
        ' 0px 1px 20px hsla(' + this.props.id * 25 + ', 80%, 30%,1) inset',
      transition: 'all .3s  '
    }
    return (
      <div className="note" onClick={   this.props.togglePiano} style={this.props.note ? activeStyle : Style}>
        {this.props.note}
        <div className={this.props.pending===this.props.index && this.props.selected===this.props.id ? "noteactive" : ""}  ></div>
      </div>
    )
  }
}

export default Note;


