import React from 'react';
import Note from './Note';
import Icon from './Icon';

class Synth extends React.Component {
  handleSynthName = (e) => {
    this.props.changeSynthName(e.target.value,this.props.id);
  }
  render() {
    let Style = {
      backgroundImage: 'linear-gradient(rgba(0,0,0,0),rgba(0,0,200,0))',
      backgroundColor: 'hsla('+this.props.id*25+', 74%, 54%,0.9)',
    }
    return (
      <div className="machine">
        <div className={this.props.selected === this.props.id? "synthesizer active": "synthesizer"} style={
          this.props.selected === this.props.id? Style : {'color':'white'}}>
          <input onChange={this.handleSynthName}  className="synthName" type="text" placeholder={this.props.synthName}   />
          <div>  
          <button onClick={() => this.props.muteSynth(this.props.id)} className={this.props.muted ? "buttons active": "buttons"}  ><Icon name="speaker" color="white" size="12"/></button>
            <button className="buttons" onClick={() => this.props.togglePiano(this.props.id, this.props.selected ,false)} > <Icon name="media" color="white" size="12"/></button>
            <button className="buttons" onClick={() => this.props.editSynth(this.props.id)}> <Icon name="settings" color="white" size="12"/></button>
            <button className="buttons" onClick={() => this.props.editFX(this.props.id)} >FX</button>
            <button className="buttons" onClick={() => this.props.removeSynth(this.props.id)}><Icon name="close" color="white" size="12"/></button>
          </div>
        </div>
        <div className="sequencer">
          {this.props.bars.map((item, index) => {
            return <Note 
                      key={index} 
                      index={index}  
                      id={this.props.id} 
                      selected={this.props.selected} 
                      pending={this.props.pending} 
                      togglePiano={() => this.props.togglePiano(this.props.id,index,true)} 
                      note={item[1]} />;
          })}
        </div>
      </div>
    )
  }
}

export default Synth;


