import React from 'react';

class Piano extends React.Component {
  constructor() {
    super();
    this.state = {
      octave: "2",
      noteSet: true
    }
  }
  handleNoteClick(e) {
    let newNote = e.target.innerText.trim()  +this.state.octave;    
    if (this.props.noteId==="") {
        this.props.noteChange(newNote);
        this.props.buffered?
        this.props.synth.triggerAttackRelease ( newNote,"8n" ): null;
    } else {
        this.props.noteChange( "");
    }
    this.setState ({
          noteSet: !this.state.noteSet
        });
  }
  handleOctave(e) {
    this.setState ({
      octave: e.target.value
    });
  }
  render() {
    if (!this.props.showPiano) {
      return null;
    }
    const KEYS = ["C", "C#", "D", "D#", "E", "F", "F#", "G", "G#", "A", "A#", "B"];
    return (
      <div className="controls">
        <div>
        Octave
        <input onChange={(e) => this.handleOctave(e)} className="ctrl" type="number" defaultValue={this.state.octave} min="0" max="9" />
        </div>
        <div>
        Length
        <select className="ctrl">
         <option >Full</option>
         <option>Half</option>
         <option>Quarter</option>
         <option>Eighth</option>
         <option>Sixteenth</option>
       </select>
        </div>
        <div className="piano">
          {
            KEYS.map((item, index) => {
              return <div key={index} onClick={(e) => { this.handleNoteClick(e) }} className="key">
                <span key={index} >
                  {item}
                </span></div>;
            }
            )
          }
        </div>
        <div>
        <button className="key clear" onClick={() => this.props.togglePiano( this.props.synthId ,0,false)}></button>
        </div>
      </div>
    )
  }
}

export default Piano;


