import React from 'react';
import Tone from 'tone';

class LevelMeter extends React.Component {
   componentDidMount = () => {
     requestAnimationFrame(this.tick);
    }
   tick = () => {
    const { width, height, meter } = this.props;
    const context = this.refs.canvas.getContext("2d");
    const level =Tone.dbToGain(meter.getLevel());
    this.setState({ level });

    context.clearRect(0, 0, width, height);
    context.save();

    const meterGraident = context.createLinearGradient(0, 0, width, height);
    meterGraident.addColorStop(0, "#FF0202");
    meterGraident.addColorStop(0.8, "#02FF24");
    meterGraident.addColorStop(1, "#BFFF02");
 
    context.fillStyle = meterGraident;
    context.fillRect(0, 0,width, height);
    context.fillStyle = "black";    
    context.fillRect(0,(height * level)*-1,  width, height);
    context.restore();  
    requestAnimationFrame(this.tick);
  }

  render() {
    const { width, height } = this.props;
    return (
      <div className="container">
      <canvas
        ref="canvas"
        width={width}
        height={height}
      />
      <div className="scanvas">
      </div>
      </div>
    );
  }
}

export default LevelMeter;


