import React from 'react';
//import Tone from 'tone';

class Transport extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      pos: 0
    }
  }
  componentDidMount = () => {
  // requestAnimationFrame(this.tick);

  }

tick = () => {
  
  //if (parseInt(Tone.Transport.position.slice(4,5))===0) {
  this.setState({ pos: this.state.pos+1 });
  if (this.state.pos >=   8 ) { 
    this.setState({ pos: 0 })
  }
 
//}
//console.log()
//requestAnimationFrame(this.tick)
//setTimeout( () => requestAnimationFrame(this.tick),( Tone.Transport.bpm.value));

}

  render () {
    
      const width = this.props.width? this.props.width : 8;
      const steps = new Array(parseInt(width,10)).fill();
      //console.log(this.props.index);
      // className={x === this.state.xpos? "active":"transport" } 
    return (

        <div className="machine">
        <div>    </div>
        <div className="sequencer transport">
            { steps.map((_, x) => (
                <div id={`test`+x}
                    key={x} className="transport"
                   >
                </div>
            ))}
       
        </div>
      </div>
    )
  }
}

export default Transport;


