import React from 'react';
import Knob from './Knob';

class FXControls extends React.Component {
  setOption = (evt, osc, value, id) => {
    console.log (evt, osc, value, id);
    this.props.songData[this.props.synthId].effectList[id].wet = value/100;
    if (this.props.effects[this.props.synthId][id])
    this.props.effects[this.props.synthId][id].set({wet: value/100});
//    console.log(this.props.songData[this.props.synthId].effectList[id].wet);
  }
  render() {
    if (!this.props.toggleFXControls ) {
      return null;
    }
    if (this.props.songData[this.props.synthId] && !Object.keys(this.props.songData[this.props.synthId].effectList).length) {
      return null;
    }
    let sound =  this.props.songData[this.props.synthId]? this.props.songData[this.props.synthId].effectList : null;
    
    const knobSize     = 0.1 * 350;

    return (
      <div>
      <div className="controls"  >

           <fieldset>
          <legend>Effects</legend>
          <div>
 
            {sound ?
              Object.keys(sound).map((item, key) => { 
                return (
                  <div key={key} className="control">{item}
                    <Knob 
                    value={parseInt(sound[item].wet * 100,10)} 
                    min={1} 
                    max={100}
                    step="1" 
                    onChange={(value) => this.setOption("", "", value, item)} 
                    numTicks={15}
                    angle={270}
                    size={knobSize}     />
                  </div>
                );
              }) : ""}
          </div>
        </fieldset>
      </div>
      </div>
    ) 
  }
}

export default FXControls;


