import React from 'react';
import Knob from './Knob';
import Icon from './Icon';

class Display extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selected: 0,
            newValue: null, //this.props.min,
            showSplash: true
        }
        
    }
 
    setOption = (value) => {
        
        let step = this.props.step? this.props.step : 0;
        //if (this.props.step) {       value= parseInt(value % 8)*4; }
        //console.log(value);
        this.props.min?
            this.setState({            newValue: value        }):
            this.setState({            selected: value,
            selectedName: this.props.menuItems.find((item,index) => {return index===value})
            });

        
    }
componentWillReceiveProps() {
    this.setState({            newValue: null       });
}
    componentDidMount() {
        setTimeout(() => this.setState({ showSplash: false }), 2500)
    }
    render() {
      //  console.log(this.props.length);
        return (
 
            <div className="display">
       

                <div id="screen" className="screen  ">
                 
                    <div className="ledback">
                    
                    {this.props.name?
                    <div className="flx jc ac vc" style={{color:"black",boxShadow: "0 0 1px 2px black inset" }}>{this.props.name}</div> : 
               ""
                }
                        {this.state.showSplash ?
                            <div className="splash"><span>bbx SynthSequencer</span></div> :
                            
                            this.props.menuItems.map((item, key) => {
                            //    console.log(item);
                                return (
                                    <div key={key} onClick={() => this.props.switchMenu(key,this.props.name,item)} className={key === this.state.selected ? "screenmenu active" : "screenmenu"} >{this.state.newValue?  this.state.newValue: item}</div>
                                )
                            })
                        }
                  
                    </div>
                </div>

            <div className="flx    flxcol"  style={{  gridRow: '1',gridColumn: '2' }} >                
            
                <Knob
                    size={90}
                    numTicks={0}
                    degrees={330}
                    style={{"transition":"none"}}
                    min={this.props.min}
                    max={this.props.length}
                    value={this.props.menuItems[0]}
                    onChange={this.setOption} />
<div className="flx   jc">
            <button style={{  width: '3em' }} onClick={() => this.props.switchMenu(-1)}><Icon name="home" color="white" size="12" /></button>    <button style={{ width: '5em' }} onClick={() => this.props.switchMenu(this.props.min?  this.state.newValue: this.state.selected,this.props.name,this.state.selectedName)}>Set</button>
     </div>
                </div>
                
                </div>
        )
    }
}

export default Display;


