class SequencerControls extends React.Component {
  constructor(props) {
    super(props);
    this.state = { menu: 0,page: 0 };
    this.page_size = 10;
    this.menuItems = [
      { name: "VOLUME", data: [this.props.volume], min: -100, max: -25 },
      { name: "BPM", data: [this.props.bpm], min: 40, max: 300 },
      { name: "BARS", data: [this.props.bars], min: 4, max: 32, step: 4 },
      { name: "KIT", data: ["KIT1","KIT2","KIT3","KIT4"] },
      { name: "PRESET", data: PRESETS.map(item => item.name) },
      { name: "EFFECTS", data: EFFECTS.map(item => item.name)},
      { name: "SONG", data: [ "A","","","" ]  }, //this.props.songData[0].bars
      { name: "PATTERN", data: ["A","B","C","D","E","F"] },
      { name: "HELP", data: ["Make music!  "] }
    ];    

    this.renderMenu = this.menuItems.map((item) => item.name);
    this.renderMenuLength = this.renderMenu.length;
    this.renderMenuName = "";
    this.renderMenuMin = "";
    this.renderMenuStep = "";

  }


  switchMenu = (val, name,item) => {
    
   // console.log(val, name,item);
  
    if (val === -1) {
      this.setState({page: 0});
      this.renderMenu = this.menuItems.map((item) => item.name);
      this.renderMenuLength = this.renderMenu.length;
      this.renderMenuName = "";
      this.renderMenuMin = "";
    } else {
      if (name === "PRESET") {        
        if(item===">>>") {
          this.setState({page: this.state.page+1});    
          this.switchMenu(4)
          
        } else {         
        this.props.addSynth(PRESETS.findIndex((fitem) => {return fitem.name===item}) )
        this.switchMenu(-1);
        }
      } else if (name === "BARS") {
        this.props.changeBars(val)

        this.switchMenu(-1);
      } else if (name === "BPM") {
        this.props.changeBPM(val)
        this.switchMenu(-1);
      } else if (name === "VOLUME") {
        this.props.changeVolume(val)
        this.switchMenu(-1);
      } else if (name === "KIT") {
        this.props.changeSongData(  JSON.stringify(songPreset) );
        this.switchMenu(-1);
      }
      else if (name === "SONG") {
        alert("Not yet available.")
        this.switchMenu(-1);
      } else if (name === "PATTERN") {
        alert("Not yet available.")
        this.switchMenu(-1);
      }
      else if (name === "EFFECTS") {
        if(item===">>>") {
          this.setState({page: this.state.page+1});          
          this.switchMenu(5);          
        } else {         
        this.props.addEffect(EFFECTS.findIndex((fitem) => {return fitem.name===item}) )
        this.switchMenu(-1);
        }
        
      } 
      else {
     //   console.log(this.menuItems[val].data);
        this.renderMenuName = this.menuItems[val].name;
        switch (this.menuItems[val].name) {
 
          case 'PRESET':
          case 'EFFECTS':
          case 'PATTERN':
          // #############pagination
            this.renderMenu = this.menuItems[val].data.slice(this.state.page * this.page_size, (this.state.page + 1) * this.page_size)  ;
            // render only on first page.
            if (this.renderMenu.length===10)
            this.renderMenu.push(">>>")
            this.renderMenuLength = this.renderMenu.length;
            break;
          default:
            this.renderMenu = this.menuItems[val].data;
            this.renderMenuLength = this.menuItems[val].max;
            this.renderMenuMin = this.menuItems[val].min;
            this.renderMenuStep = this.menuItems[val].step;
            break;
        }
      }
    }
    this.setState({ menu: val });
  }
showFile = () => {
  var file    = document.querySelector('input[type=file]').files[0];
  
  var reader  = new FileReader();
  reader.onloadend = () => {
    this.props.changeSongData( reader.result);
}
  reader.addEventListener("load", function () {
  }, false);
  if (file) {
    reader.readAsText(file);
  }
  
}
  render() {
    let processedSongData = JSON.parse(JSON.stringify(this.props.songData));
    for (let i = 0; i < processedSongData.length; i++) {
      processedSongData[i].instrument = "";
      processedSongData[i].tonePart = "";
      processedSongData[i].effects = [];
      processedSongData[i].bars = processedSongData[i].bars.slice(0,this.props.bars);
    }

    let data = "text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(processedSongData));

    /** ##################################  
    /** 
     * TODO/IDEAS:  Shuffle, Patterns, Slide,  equalizer, Swing MIDI PAN LOOP MORPH MIX
     * 
    let data = "text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(this.props.songData));
    const isFox = typeof InstallTrigger !== 'undefined'; //navigator.indexOf('firefox');
        { isFox? <h5 style={{color:"red"}}>Firefox might sound crappy, due to a webaudio bug</h5>:  ""  }
     <a href={`data:${data}`} download="data.json">Save <Icon name="download" color="white" size="16"/> </a>

                 <input onClick={this.props.changeBPM} className="ctrl" type="number" defaultValue={this.props.bpm} min="40" max="200" />
            <input onChange={this.props.changeVolume} className="ctrl" type="number" defaultValue="70" step="10" min="0" max="100" />
            <input onChange={this.props.changeBars} className="ctrl" type="number" defaultValue="8" step="4" min="4" max="32" />

    */

    return (
      <div className="sequencerControls">
        <div className="menu">
          <div className="flx ac jc flxcol ">
          <div className=" flx flxcol ac jc"><img alt="" src={logo} width="100" />
            <div className="logo" >synthsequencer </div><div className="logo ">sp{this.props.bars}</div>             </div>
          </div>

          <Display
            name={this.renderMenuName}
            volume={this.props.volume}
            menuItems={this.props.buffered? this.renderMenu: ["Loading.."]}
            length={this.renderMenuLength}
            min={this.renderMenuMin}
            step={this.renderMenuStep}
            switchMenu={(val, name,item) => this.switchMenu(val, name,item)}
          />

          <div className="flx jc sa" >
          <div className="flx flxcol sa jc" >
          <div className="flx ac jc" >
            <div    >
              <label htmlFor="files" style={{ display: "block" }} className="ctrl"><Icon name="upload" color="white" size="16" /></label>
              <input onChange={this.showFile} id="files" style={{ display: "none" }} type="file" />
            </div>
            <a   className="ctrl"  href={`data:${data}`} download="data.json"><Icon name="download" color="white" size="16" /> </a>
            <a className="ctrl"  ><Icon name="file-music" color="white" size="16" /></a>
            <a onClick={() => this.switchMenu(8)} className="ctrl" >? </a>
            </div>
            
            <div className="flx jc ac" style={{flexGrow: "1"}}>
            
            <button className="play" onClick={this.props.playSong}>{this.props.playing ? <Icon name="controller-stop" color="black" size="32" /> : <Icon name="controller-play" color="darkgreen" size="32" />}</button>
            <button className="play" ><Icon name="controller-record" color="darkred" size="32" /> </button>
            
</div> 
          </div>
          <div className="flx flxcol ac jc sa" >
          <LevelMeter  meter={this.props.meter} width={32} height={140} />
          lvl
          </div>
          
        </div>
       
          
        </div>
      </div>
    )
  }
}

export default SequencerControls;


